<template>
<div class="p-5 rounded mt-3 bg-white shadow-material">
    <div class="mb-3" v-for="field in formFields" :key="field.label">
        <template v-if="field.element">
            
            {{field.label}}: {{field.value}}

            <component 
                v-model="field.value"
                :is="field.element" 
                v-bind="field.elementProps"
            >
            </component>
            
        </template>
        <template v-else-if="field.inputComponent === 'SelectFromList'">
            {{field.label}}: {{field.value}}
            <select-from-list
                v-model="field.value" 
                :getUrl="correspondence[field.model].URLs.get"
                :correspondenceOfFieldsAndRusLabels="correspondence[field.model].fields"
            ></select-from-list>
        </template>
        <template v-else>
            <LabelInput :labelText="`${field.label} ${field.units ? '['+field.units+']' : ''}`" v-model="field.value"/>
        </template>
    </div>

    <slot></slot>

    <button class="btn btn-primary" @click="create()">Создать</button>
</div>
</template>

<script>

import LabelInput from '@/components/elements/LabelInput.vue'
import FileUpload from '@/components/elements/FileUpload.vue'
import SelectFromList from '@/components/elements/SelectFromList.vue'

const keyValueObjectFromFormFields = require('@/utility/keyValueObjectFromFormFields.js')
const correspondence = require('@/utility/correspondence')
const uniqueId = require('lodash.uniqueid')

export default {
    name: 'create-form',
    components:{ LabelInput, FileUpload, SelectFromList },
    props:{
        formFieldsProp: Object,
    },
    data(){
        return{
            uniqueId: uniqueId(),
            formFields:{},
            correspondence
        }
    },
    methods:{
        async create(){

            const { formFields } = this

            const dataForCreate = keyValueObjectFromFormFields(formFields)

            this.$emit('create', dataForCreate)

            for(const key of Object.keys(this.formFields)){
                this.formFields[key].value = ""
            }

        }
    },
    created(){
        this.formFields = {...this.formFieldsProp}
    }
}
</script>