<template>
    <div>
        <h1 class="font-weight-light">Транспортные средства</h1>

        <router-link v-if="authUser.role === 'admin'" class="btn" :class="'btn-primary'" :to="`/createATS`">
            <i class="bi" :class="'bi-pencil-square'"></i> Добавить
        </router-link>

        <div class="card mt-4 mb-4 bg-white shadow-material" v-for="item of list" :key="item.id">

            <div class="row g-0">
                <div class="col-md-8">
                    <div class="card-body">
                        <h3 class="card-title">{{item.Marka_model}}</h3>
                        <div class="card-text pt-2">

                        <CharacteristicCard 
                            :content="item" 
                            :correspondenceOfFieldsAndRusLabels="correspondenceATS.fields"
                            style="margin-left:-18px;"
                        />

                        </div>

                        <p class="card-text mt-4">
                            <small class="text-muted">
                                <strong>Добавил:</strong> {{item.user.fullName}} <br>
                                <strong>Добавлено:</strong> <RuDate :date="item.createdAt"/>
                                <template v-if="item.createdAt!==item.updatedAt">
                                    <br><strong>Последнее изменение:</strong> <RuDate :date="item.updatedAt"/>
                                </template>
                            </small>
                        </p>

                    </div>

                </div>
                <div class="col-md-4 p-4 text-center">

                    <template v-for="image in item.ATSFiles" :key="image.name">
                        <img :src="`${env.VUE_APP_API}${image.name}`" class="border rounded" style="width:100%;">
                    </template>
                    
                    <a class="btn btn-primary mt-4"
                        v-if="item.Three_d_model_link"
                        :href="item.Three_d_model_link" 
                        target="_blank"
                    >
                        <i class="bi bi-badge-3d"></i> Открыть 3D модель
                    </a>
                    <!-- <button class="btn btn-primary mt-4" data-toggle="modal" :data-target="`#threed-modal-${item.id}`">Открыть 3D модель</button> -->
                    
                </div>
            </div>

            <div class="card-footer text-muted" v-if="authUser.role === 'admin'">

                <a class="card-link text-danger" style="cursor:pointer;" @click="remove(item.id)">
                    <i class="bi bi-trash"></i> Удалить
                </a>
                <router-link class="card-link text-secondary" style="cursor:pointer;" :to="`/updateATS/${item.id}`">
                    <i class="bi bi-pencil"></i> Редактировать
                </router-link>

            </div>


            <!-- Modal -->
            <!-- <div class="modal fade" :id="`threed-modal-${item.id}`" tabindex="-1" role="dialog" aria-labelledby="threed-modal-label" aria-hidden="true">
                <div class="modal-dialog modal-lg" role="document">
                    <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="threed-modal-label">3D модель {{item.Marka_model}}</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body" style="height:600px;">
                        <template v-if="item.id===1">
                            <img :src="require(`@/assets/track3d.jpg`)" style="width:100%;">
                        </template>
                        <template v-else>
                            3D модель пока что отсутствует.
                        </template>
                    </div>
                    </div>
                </div>
            </div> -->

        </div>
        
    </div>






</template>

<script>

import Axios from '@/axios'

import CharacteristicCard from '@/components/basic/CharacteristicCard.vue'
import RuDate from '@/components/elements/RuDate.vue'

import { mapGetters } from 'vuex'

const uniqueId = require('lodash.uniqueid')
const correspondence = require('@/utility/correspondence')

export default {
    components: { CharacteristicCard, RuDate },
    data(){
        return{
            list:[],
            uniquePostfix: uniqueId(),
            correspondenceATS: correspondence.ATS
        }
    },
    computed:{
        ...mapGetters(['authUser']),
    },
    methods:{
        async remove(id){

            if(!confirm("Вы подтвреждаете удаление материала?")) return;

            try{

                await Axios.delete(`ATS/${id}`)
                this.list = this.list.filter(content => content.id !== id)

            }catch(e){
                alert(`Ошибка: ${e}`)
            }

        }
    },
    async created(){
        this.list = (await Axios.get('ATS')).data
    }
}
</script>