<template>
    {{ruDate}}
</template>
<script>
export default {
    props: ['date'],
    data(){
        return{
            ruDate: ""
        }
    },
    created(){

        const formatter = new Intl.DateTimeFormat("ru",{
            year: "numeric",
            month: "long",
            day: "numeric",
            hour: "numeric",
            minute: "numeric",
        });

        this.ruDate = formatter.format(new Date(this.date))

    }
}
</script>
