<template>
    <div>
        <a class="link-secondary" style="color:#404040;" @click="content!==null ? content = null : show(url, contentId)">
            <i class="bi" :class="content!==null ? 'bi-caret-down-fill': 'bi-caret-right-fill'"></i>
            <slot>Показать</slot>
        </a>
    </div>
    <CharacteristicCard 
        v-if="content!==null"
        :content="content" 
        :correspondenceOfFieldsAndRusLabels="correspondenceOfFieldsAndRusLabels"
    />
</template>

<style scoped>
    a {
        cursor: pointer;
    }
</style>

<script>
import Axios from '@/axios'
import CharacteristicCard from '@/components/basic/CharacteristicCard.vue'

export default {
    components: { CharacteristicCard },
    props:{
        url: String,
        contentId: Number,
        correspondenceOfFieldsAndRusLabels: Object
    },
    data(){
        return{
            content: null
        }
    },
    methods: {  
        async show(url, id){
            this.content = (await Axios.get(`${url}/${id}`)).data
        }
    }
}
</script>