import Axios from '@/axios'

export default {
    state: () => ({
        loadedMeasureUnits: {},
    }),
    getters: {
        getLoadedMeasureUnits: state => state.loadedMeasureUnits,
        getLoadedMeasureUnitByGlobalId: (state) =>  (global_id) => state.loadedMeasureUnits[global_id]
    },
    mutations: {
        setMeasureUnits: (state, measureUnits) => {

            for(const measureUnit of measureUnits)
                state.loadedMeasureUnits[measureUnit.global_id] = measureUnit

        }
    },
    actions: {
        async loadMeasureUnits({commit}){

            const measureUnits  = (await Axios.get(`measure_unit`)).data

            commit('setMeasureUnits', measureUnits)

        },
    },
};