<template>
<div class="container-fluid">
    <div class="row no-gutter">
        <div class="col-md-6 d-none d-md-flex bg-image"></div>
        <div class="col-md-6 bg-light">
            <div class="login d-flex align-items-center py-5">
                <div class="container">
                    <div class="row">
                        <div class="col-lg-10 col-xl-7 mx-auto">
                            <template v-if="signin">
                                <h3 class="display-4">Войти</h3>
                                <SignIn v-on:sign-in="login($event)"/>
                                <button class="btn btn-link btn-block  mb-2 " @click="signin = false">Создать аккаунт</button>
                            </template>
                            <template v-else>
                                <h3 class="display-4">Создание аккаунта</h3>
                                <CreateProfile v-on:profile-has-been-created="login($event)"/>
                                <button class="btn btn-link btn-block  mb-2" @click="signin = true">Войти в существующий аккаунт</button>
                            </template>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>

</template>

<script>
import SignIn from '@/components/login/SignIn.vue'
import CreateProfile from '@/components/login/CreateProfile.vue'

import Axios from '@/axios'

export default {
    components: { SignIn, CreateProfile },
    data(){
        return{
            signin: true
        }
    },
    methods:{
        async login({login, password}){
        
            const res = (await Axios.post("users/signin", {
                login,
                password
            })).data

            if(res.error){
                alert(res.error)
            }else{
                localStorage.setItem('jwtToken', res.token)
                Axios.defaults.headers.common['Authorization'] = res.token
                //this.$router.push('/')
                this.$router.go()
            }

        }
    }
}
</script>

<style scoped>
.login,
.image {
  min-height: 100vh;
}

.bg-image {
  background-image: url('~@/assets/header-bg.jpg');
  background-size: cover;
  background-position: center center;
}
</style>