<template>
    <div>

        <button 
            type="button" 
            class="btn btn-primary" 
            @click="showList=!showList"
        >
            Выбрать из списка
        </button>

        <button 
            type="button" 
            class="btn btn-link" 
            @click="idHasBeenSelected('')"
            v-if="modelValue"
        >
            Сбросить
        </button>

        <div 
            v-if="showList"
            class="shadow-sm border border-4 rounded overflow-auto pl-4 pr-4 pt-4 mt-2 bg-white" 
            style="max-height:600px;"
        >
            <!-- <ListWithSelect 
                :getUrl="getUrl"
                :correspondenceOfFieldsAndRusLabels="correspondenceOfFieldsAndRusLabels"
                @id-has-been-selected="idHasBeenSelected($event)"
            /> -->

        <crud-list
            :key="uniqueId" 
            :getUrl="getUrl"
            :postUrl="getUrl"
            :canSelect="true"
            :correspondenceOfFields="correspondenceOfFieldsAndRusLabels"
            @id-has-been-selected="idHasBeenSelected($event)"
        ></crud-list>
            
        </div>
    </div>
</template>

<script>
//import ListWithSelect from '@/components/basic/ListWithSelect/List.vue'

const uniqueId = require('lodash.uniqueid')

export default {
    name: 'select-from-list',
    props:{
        modelValue: String,
        getUrl: String,
        correspondenceOfFieldsAndRusLabels: Object
    },
    data(){
        return{
            showList: false,
            uniqueId: uniqueId(),
        }
    },
    methods:{
        idHasBeenSelected(id){
            this.showList = false
            this.$emit('update:modelValue', id)
        }
    },
}
</script>