const allModels = require("../../../transport_backend/model_description/data/allModelsDescription")

module.exports = {
    ...allModels,
    ATS: {
        URLs: {
            get: 'ATS',
            post: 'ATS'
        },
        fields: {
            Marka_model: { label: "Марка/модель", value: "" },
            Three_d_model_link: {
                label: "3D модель", 
                value: "",
                notShow: true
            },
            ...Object.values(allModels)
                .filter(model => !['measure_unit', 'tip_shin', 'dvigatel', 'cilindry', 'kabina', 'bortovoe_napryazhenie'].includes(model.modelName))
                .reduce((acc, val) => {
                    acc[`${val.modelName}_id`] = {
                                label: val.ruModelName,
                                value: "",
                                model: val.modelName,
                                inputComponent: "SelectFromList",
                                outputComponent: "ShowCharacteristic",
                            }
                    return acc
                }, {})
        }
    },
}