import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import CrudList from '@/components/basic/CrudList.vue'
import ShowCharacteristic from '@/components/basic/ShowCharacteristic.vue'

import 'bootstrap/dist/js/bootstrap.bundle'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap-icons/font/bootstrap-icons.css'
import './App.css'

const app = createApp(App)
.component('crud-list', CrudList)
.component('show-characteristic', ShowCharacteristic)
.use(store)
.use(router)

app.config.globalProperties.env = process.env

app.mount('#app')