<template>
    <div class="card" style="margin-left:10px;">
        <div class="card-body">
            <p class="card-text">
                <template v-for="[fieldKey, field] in Object.entries(correspondenceOfFieldsAndRusLabels)" :key="fieldKey">

                <template v-if="content[fieldKey] && field.notShow!==true">  

                    <template v-if="field.outputComponent === 'ShowCharacteristic'">

                        <show-characteristic 
                            :url="correspondence[field.model].URLs.get" 
                            :contentId="content[fieldKey]"
                            :correspondenceOfFieldsAndRusLabels="correspondence[field.model].fields"
                        >
                            {{field.label}}
                        </show-characteristic>

                    </template>
                    <template v-else>
                        {{field.label}}: 
                        <strong>
                            {{content[fieldKey]}} <UnitsByGlobalId :global_id="field.units_gloabal_id"/>
                        </strong>
                        <br>
                    </template>

                </template>
                    
                
                </template>
            </p>
        </div>
    </div>
</template>

<script>

import UnitsByGlobalId from '@/components/elements/UnitsByGlobalId.vue'

const correspondence = require('@/utility/correspondence')

export default {
    components: { UnitsByGlobalId },
    props:{
        content:Object,
        correspondenceOfFieldsAndRusLabels:Object
    },
    data(){
        return{
            correspondence
        }
    }
}
</script>