<template>
    {{measureUnit}}
</template>

<script>

import { mapGetters } from 'vuex'

export default {
    props: ['global_id'],
    data(){
        return{
            measureUnit: ""
        }
    },
    computed: {
        ...mapGetters(['getLoadedMeasureUnitByGlobalId'])
    },
    created(){
        if(this.global_id){
            this.measureUnit = this.getLoadedMeasureUnitByGlobalId(this.global_id)?.NATIONAL
        }
    }
}
</script>