<template>
<div>
    <label :for="`input-with-label-${uniquePostfix}`" class="form-label">{{labelText}}</label>
    <input 
        type="text" 
        class="form-control" 
        :id="`input-with-label-${uniquePostfix}`" 
        :value="modelValue"
        @input="$emit('update:modelValue', $event.target.value)"
    >
</div>
</template>

<script>

const uniqueId = require('lodash.uniqueid')

export default {
    props: ['modelValue', 'labelText'],
    data(){
        return{
            uniquePostfix: uniqueId()
        }
    }
}
</script>

