<template>
    <div>
        <form class="form-group" enctype="multipart/form-data">

            <label :for="`file-upload-${uniqueId}`" class="btn btn-outline-primary custom-file-upload" >
                <i class="bi bi-upload"></i> Выбрать файл
            </label>
            <input :id="`file-upload-${uniqueId}`"  type="file" @change="processFile($event)"/>

        </form>
    </div>
    <div v-for="file in files" :key="file.file.name">
        <img :src="file.dataURL" class="border rounded" style="width:100%;max-width:300px;">
        <button class="btn btn-danger" @click="detachFile(file.file)">Открепить</button>
    </div>
</template>


<script>

/**
 * Процесс работы с файлами.
 * 
 * Добавление записи -> файлы загружаются одновременно с добавлением записи -> добавляются в соответсвующую таблицу имеющую связь с записью
 * 
 * Изменение записи -> набор файлов для записи приводится в соответсвие новоому набору -> открепленные файлы удаляются
 */

import Axios from '@/axios'
const uniqueId = require('lodash.uniqueid')

export default {
    name: "FileUpload",
    data(){
        return {
            uniqueId: uniqueId(),
            files: []
        }
    },
    props:{
        filesFromServer: Array,
    },
    methods:{
        detachFile(file){

            this.files = this.files.filter(item => item.file.name !== file.name)

            this.$emit('file-detach-from-upload', file);

        },
        addFile(file){

            const reader = new FileReader();
            reader.readAsDataURL(file);

            reader.onload = () => {
                this.files.push({
                    file: file,
                    dataURL: reader.result
                });
            }

            this.$emit('file-ready-for-upload', file);

        },
        processFile(event){

            const file = event.target.files[0];

            this.addFile(file)
            

        },
        async processFileFromServer(fileName){

            const fileBuffer = (await Axios.get(`${this.env.VUE_APP_API}${fileName}`, { responseType:"arraybuffer" })).data

            const file = new File([fileBuffer], fileName)

            this.addFile(file)

        }
    },
    async created(){

        for(const file of this.filesFromServer){
            await this.processFileFromServer(file.name)
        }

    }
}
</script>


<style scoped>

input[type="file"] {
    display: none;
}

.custom-file-upload{
   
    display: inline-block;
}


</style>