<template>
   <p class="text-muted mb-4"></p>
   
        <div class="form-group mb-3">
            <!-- <label for="login" class="form-label">Логин</label> -->
            <input 
                type="text" 
                class="form-control px-4" 
                id="login" 
                placeholder="E-mail" 
                v-model="login"
            >
        </div>
        <div class="form-group mb-3">
            <!-- <label for="password" class="form-label">Пароль</label> -->
            <input 
                type="password" class="form-control px-4" 
                id="password" 
                placeholder="Пароль" 
                v-model="password"
            >
        </div>
        <div class="mb-3">
            <button class="btn btn-primary btn-block " @click="signIn()">Войти</button>
        </div>
    
</template>

<script>
export default {
    data(){
        return{
            login: "",
            password: ""
        }
    },
    methods:{
        async signIn(){

            const {login, password} = this

            this.$emit('sign-in', {
                login,
                password
            })
    
        }
    }
}
</script>