<template>
    <div class="card mb-4 mt-4 bg-white shadow-material">
        <div class="card-header">
            ID: {{content.id}}
        </div>
        <div class="card-body">
            <p class="card-text">
                <CharacteristicCard
                    :content="content" 
                    :correspondenceOfFieldsAndRusLabels="correspondenceOfFieldsAndRusLabels"
                    style="margin-left:-18px;margin-top:-20px;margin-bottom:-20px"
                />
            </p>
            <p class="card-text">
                <small class="text-muted">
                    <strong>Добавил:</strong> {{content['user.fullName']}}<br>
                    <strong>Дата создания:</strong> <RuDate :date="content.createdAt"/>
                    <template v-if="content.createdAt!==content.updatedAt">
                        <br><strong>Последнее изменение:</strong> <RuDate :date="content.updatedAt"/>
                    </template>
                </small>
            </p>
        </div>
        <div class="card-footer text-muted">

            <button v-if="canSelect" type="button" class="btn btn-primary mr-4" @click="$emit('id-has-been-selected', content.id)">
               <i class="bi bi-check2"></i> Выбрать
            </button>

            <a class="card-link text-danger" style="cursor:pointer;" @click="$emit('remove', content.id)">
                <i class="bi bi-trash"></i> Удалить
            </a>
            <a class="card-link text-secondary" style="cursor:pointer;" data-toggle="modal" :data-target="`#edit-modal-${uniquePostfix}`">
               <i class="bi bi-pencil"></i> Редактировать
            </a>
        </div>
    </div>

    <!-- Modal -->
    <div class="modal fade" :id="`edit-modal-${uniquePostfix}`" :ref="`edit-modal-${uniquePostfix}`" tabindex="-1" role="dialog" aria-labelledby="edit-modal-label" aria-hidden="true">
        <div class="modal-dialog modal-lg" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="edit-modal-label">Редактировать</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <UpdateForm 
                        :contentId="content.id" 
                        :formFieldsProp="fieldsWithValue"
                        v-on:update="update($event)"
                    />
                </div>
            </div>
        </div>
    </div>

    
    
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

import UpdateForm from '@/components/basic/UpdateForm.vue'
import ShowCharacteristic from '@/components/basic/ShowCharacteristic.vue'
import CharacteristicCard from '@/components/basic/CharacteristicCard.vue'
import UnitsByGlobalId from '@/components/elements/UnitsByGlobalId.vue'
import RuDate from '@/components/elements/RuDate.vue'

const uniqueId = require('lodash.uniqueid')
import $ from 'jquery'; 

const correspondence = require('@/utility/correspondence')

export default {
    components:{ UpdateForm, ShowCharacteristic, UnitsByGlobalId, RuDate, CharacteristicCard },
    props:{
        content:Object,
        correspondenceOfFieldsAndRusLabels:Object,
        canSelect:{
            type: Boolean,
            default: false
        }
    },
    data(){
        return{
            fieldsWithValue:{},
            uniquePostfix: uniqueId(),
            correspondence,
        }
    },
    methods:{
        update(data){

            $(`#edit-modal-${this.uniquePostfix}`).modal('hide');

            this.$emit('update', data)
        },
        ...mapActions(['getMeasureUnitByGlobalId'])
    },
    created(){

        
        for(const [fieldKey, fieldValue] of Object.entries(this.correspondenceOfFieldsAndRusLabels)){

            this.fieldsWithValue[fieldKey] = {
                ...fieldValue,
                value: this.content[fieldKey]
            }

        }

    }
}
</script>