<template>

    <button class="btn" :class="showCreateForm ? 'btn-danger' : 'btn-primary'" @click="showCreateForm = !showCreateForm">
        <i class="bi" :class="showCreateForm ? 'bi-x' : 'bi-pencil-square'"></i> {{showCreateForm ? 'Отмена' : 'Создать'}}
    </button>

    <create-form 
        :key="uniqueId"
        v-if="showCreateForm"
        :postUrl="postUrl" 
        :formFieldsProp="correspondenceOfFields"
        @create="create($event)"
    ></create-form>

    <div v-for="item of list" :key="item.id">
        <Card 
            :content="item" 
            :correspondenceOfFieldsAndRusLabels="correspondenceOfFields"
            :canSelect="canSelect"
            @id-has-been-selected="$emit('id-has-been-selected', $event)"
            @remove="remove($event)"
            @update="update($event)"
        />
    </div>
</template>

<script>
import Axios from '@/axios'
import Card from '@/components/basic/Card.vue'
import CreateForm from '@/components/basic/CreateForm.vue'
const uniqueId = require('lodash.uniqueid')

export default {
    name: "crud-list",
    components: { CreateForm, Card },
    props:{
        getUrl: String,
        postUrl: String,
        correspondenceOfFields: Object,
        canSelect: {
            type: Boolean,
            default: false
        }
    },
    data(){
        return{
            showCreateForm: false,
            list:[],
            uniqueId: uniqueId()
        }
    },
    methods:{
        async create(data){

            try{

                const newItem = (await Axios.post(this.postUrl || this.getUrl, data)).data

                this.list.unshift(newItem)

                this.showCreateForm = false

            }catch(e){
                alert(`Ошибка: ${e}`)
            }

        },
        async update(data){

            try{

                await Axios.put(`${this.updateUrl || this.getUrl}/${data.contentId}`, data.dataForUpdate)

                this.list = this.list.map(content => {
                    if(content.id === data.contentId){
                        return {
                            ...content,
                            ...data.dataForUpdate
                        }
                    }else{
                        return content
                    }
                })
            

            }catch(e){
                alert(`Ошибка: ${e}`)
            }

        },
        async remove(id){

            if(!confirm("Вы подтвреждаете удаление материала?")) return;

            try{

                await Axios.delete(`${this.deleteUrl || this.getUrl}/${id}`)
                this.list = this.list.filter(content => content.id !== id)

            }catch(e){
                alert(`Ошибка: ${e}`)
            }

            
        }
    },
    async created(){
        this.list = (await Axios.get(this.getUrl)).data
    }
}
</script>