<template>
<div class="shadow-sm bg-light p-5 rounded mt-3">
    <div class="mb-3" v-for="field in formFields" :key="field.label">
        <template v-if="field.element">
            
            {{field.label}}: {{field.value}}

            <component 
                v-model="field.value"
                :is="field.element" 
                v-bind="field.elementProps"
            >
            </component>
            
        </template>
        <template v-else-if="field.inputComponent === 'SelectFromList'">
            {{field.label}}: {{field.value}}
            <SelectFromList
                v-model="field.value" 
                :getUrl="correspondence[field.model].URLs.get"
                :correspondenceOfFieldsAndRusLabels="correspondence[field.model].fields"
            />
        </template>
        <template v-else>
            <LabelInput :labelText="`${field.label} ${field.units ? '['+field.units+']' : ''}`" v-model="field.value"/>
        </template>
    </div>

    <slot></slot>

    <button class="btn btn-primary" @click="update()">Изменить</button>
</div>
</template>

<script>
import LabelInput from '@/components/elements/LabelInput.vue'

const keyValueObjectFromFormFields = require('@/utility/keyValueObjectFromFormFields.js')
const correspondence = require('@/utility/correspondence')
import SelectFromList from '@/components/elements/SelectFromList.vue'

export default {
    components:{ LabelInput, SelectFromList },
    props:{
        formFieldsProp: Object,
        contentId: Number
    },
    data(){
        return{
            formFields:{},
            correspondence
        }
    },
    methods:{
        async update(){

            const { formFields } = this

            const dataForUpdate = keyValueObjectFromFormFields(formFields)

            this.$emit('update', {
                contentId: this.contentId, 
                dataForUpdate
            })

        }
    },
    created(){
        this.formFields = {...this.formFieldsProp}
    }
}
</script>