import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'
import store from '@/store'

const allModels = require("../../../transport_backend/model_description/data/allModelsDescription")

const vueRoutesFactory = (model) => {
  return {
    path: `/${model.modelName}`,
    name: `${model.ruModelName || model.modelName}`,
    component: () => import('../views/ListPage'),
    meta: { requiresAuth: true, requiresAdmin: true, underDictionaryRules: true, entity: model }
  }
}

const routes = [
  {
    path: '/',
    name: 'Главная',
    component: Home,
    meta: { }
  },
  {
    path: '/createuser',
    name: 'Создать пользователя',
    component: () => import('../views/CreateUser.vue'),
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/profile',
    name: 'Профиль',
    component: () => import('../views/Profile.vue'),
    meta: { requiresAuth: true }
  },
  {
    path: '/createATS',
    name: 'Добавить транспортное средство',
    component: () => import('../components/ATS/Create.vue'),
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/updateATS/:id',
    name: 'Изменить транспортное средство',
    component: () => import('../components/ATS/Update.vue'),
    meta: { requiresAuth: true, requiresAdmin: true, }
  },
  ...Object.values(allModels).map(model => vueRoutesFactory(model))
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

router.beforeEach(async (to, from, next) => {
  //console.log(to, from, next)

  const authUser = await store.dispatch('getAuthUser');

  if(to.meta.requiresAuth !== undefined){

    if(authUser && to.meta.requiresAuth === true){
      if(to.meta.requiresAdmin === true && authUser.role !== 'admin') return;
      next()
    }

    if(!authUser && to.meta.requiresAuth === false){
      next()
    }

  }else{
    next();
  }

  
})

export default router
